import axios from 'axios';
import router from '../plugins/router.js'

//通用服务
function get({
  apiname,
  params,
  type, // 要发出去的类型
  commit,//通讯工具
  _id = null,
  dataName = null
}) {
  let url = _id ? '/api/' + dataName + '/' + _id : '/api/' + apiname
  axios({
    url,params
  }).then(
    res => {
      // console.log('get', res.data);
      if (res.data.err === 1) {
        router.push('/login')
      } else {
        commit(type, res.data.data)
      }
    }
  )
}


export {
  get
}
