import axios from 'axios';

import {
  get
} from './service.js';

import * as types from './types.js';

export default {

  [types.HOME]: ({
    commit,
    state
  }, payload) => {
    // this指向 home组件 ×
    // vm 实例 import vm from 'main.js'
    get({
      apiname: 'home',
      params: {
        _limit: 10,
        _page: 1
      },
      type: types.HOME,
      commit
    })
  },

  [types.FOLLOW]: ({
    commit,
    state
  }, payload) => {
    get({
      apiname: 'follow',
      params: {
        _limit: 8,
        _page: 1
      },
      type: types.FOLLOW,
      commit
    })
  },

  [types.COLUMN]: ({
    commit,
    state
  }, payload) => {
    get({
      apiname: 'column',
      params: {
        _limit: 6,
        _page: 1
      },
      type: types.COLUMN,
      commit
    })
  },

  [types.BANNER]: ({
    commit,
    state
  }, payload) => {
    get({
      apiname: 'banner',
      params: {
        _limit: 3,
        _page: 1
      },
      type: types.BANNER,
      commit
    })
  },

  [types.DETAIL]: ({
    commit,
    state
  }, payload) => {
    let {
      _id,
      dataName
    } = payload;

    get({
      apiname: 'detail',
      type: types.DETAIL,
      _id,
      dataName,
      commit
    })

  },

  [types.USER]: ({
    commit,
    state
  }, payload) => {
    let {
      username,
      password
    } = payload;
    let params = new URLSearchParams();
    params.append("username", username)
    params.append("password", password)
    return axios({
      url: `/api/login`,
      method: 'post',
      data: params,
    }).then(
      res => {
        //一手交数据到mutaions
        commit(types.USER, res.data)

        return res //返回回执
      }
    )
  },

  addItem: ({
    commit,
    state
  }, payload) => {

    let arr = [...state.buycar];

    let find = false;

    arr.forEach((item, index) => {
      if (item._id == payload._id) {
        item.number++; //数量递增
        find = true;
      }
    });
    if (!find) {
      payload.number = 1; //数量
      arr.push(payload);
    }
    // commit(types.ADD_ITEM,[...state.buycar])
    commit('ADD_ITEM', arr)
  },

  changeItem: ({
    commit,
    state
  }, payload) => {
    // [...state.buycar]
    // {...state}

    console.log('changeItem', payload)
    state.buycar.forEach((item, index) => {
      if (item._id == payload._id) {
        item.number += payload.num;
      }
    });
    //如果是对象，需要复制一份，不然state.buycar指向的是复制后state修改之前的引用
    commit('CHANGE_ITEM', [...state.buycar])
    // commit(types.CHANGE_ITEM,state)  ×
  },

  removeItem: ({
    commit,
    state
  }, payload) => {

    state.buycar.forEach((item, index) => {
      if (item._id == payload._id) {
        state.buycar.splice(index, 1);
      }
    });
    commit('REMOVE_ITEM', [...state.buycar]);
    // commit(types.REMOVE_ITEM,state.buycar);
  },

  clearBuycar: ({
    commit,
    state
  }) => {
    commit('CLEAR_BUYCAR', []);
  }

}
