<template>
  <div id="app">
    <loading v-show="$store.state.bLoading"/>
    <Header v-show="bNav"/>
    <!-- <transition
      enter-active-class = "animated fadeIn"
    > -->
      <router-view></router-view>
    <!-- </transition> -->
    <Footer v-show="bFoot"/>
  </div>
</template>
<script>
  import Home from '../pages/home.vue'
  import Header from './header.vue'
  import Footer from '@/layouts/footer.vue'
  import loading from '@/components/loading.vue'
  
  import {mapState} from 'vuex'
  import * as types from '../store/types.js'
  export default{
    name:'App',
    components:{
      Home,Footer,Header,loading
    },
    mounted(){
      console.log('store实例',this.$store);
    },
    
    computed:mapState([
      'bNav','bFoot'
    ]),
    
    //路由监听|属性检测|数据观测
    watch:{
      $route:{
        handler(nextValue,PrevValue){
          console.log('path变化',nextValue.path);
          
          let path = nextValue.path;//路由路径  /follow
          //修改集中式的数据
          
          if(/login|reg|detail/.test(path)){
            this.$store.commit(types.VIEW_NAV,false)
            this.$store.commit(types.VIEW_FOOT,false)
          }
          if(/home|follow|column/.test(path)){
            this.$store.commit(types.VIEW_NAV,true)
            this.$store.commit(types.VIEW_FOOT,true)
          }
          if(path.includes('user')){
            this.$store.commit(types.VIEW_NAV,false)
            this.$store.commit(types.VIEW_FOOT,true)
          }
        },
        immediate:true
      }
    },
    
  }
</script>
